/*
 * Media query to respond to a minimum size (mobile first)
 */
@mixin resp-min($size) {
   @media screen and (min-width: $size){
    @content;
  }
}

/*
 * Media query to respond to a maximum size
 */
@mixin resp-max($size) {
   @media screen and (max-width: $size){
    @content;
  }
}

/*
 * Output font size in px/rem
 */
@mixin font-size($px) {
  font-size: $px + px;
  font-size: #{$px / 16}rem;
}

/*
 * Placeholder mixin for <input>
 */
@mixin placeholder {
  $placeholders: ":-webkit-input" ":-moz" "-moz" "-ms-input";
  @each $placeholder in $placeholders {
    &:#{$placeholder}-placeholder {
      @content;
    }
  }
}

/**
 * Circle
 */
@mixin circle($size) {
  content: "";
  position: absolute;
  border-radius: 50%;
  width: $size;
  height: $size;
  left: calc(50% - #{$size/2});
  top: calc(50% - #{$size/2});
}

/**
 * Clearfix
 */
@mixin clearfix {
  &:after,
  &:before {
    content: " ";
    display: table;
  }

  &:after {
    clear: both;
  }
}
